:root {
   --menu-background-color: #FFFFFF;
   --menu-color: #000000;
   --menu-shadow: #555555;
   --side-menu-tint: rgba(255, 255, 255,1);
}
.navbar
{
   /* position: fixed; */
   z-index: 100;
   background-color: #FFFFFF;
   background-color: var(--menu-background-color);
   height: 70px;
   width: 100%;
   transition: background-color 0.5s cubic-bezier(0.29, 0.26, 0.54, 1.19);
}
.navbar nav {
   height: 100%;
}

.main-menu {
   margin: 0 20px;
   display: flex;
   position: relative;
   justify-content: space-between;
   align-items: center;
   height: 100%;
}

.main-menu ul {
   display: flex;
   list-style: none;
   justify-content: space-between;
   align-items: center;
}

.main-menu ul li a {
   padding: 0 20px;
   color: #000000;
   color: var(--menu-color);
   text-transform: uppercase;
}

.side-menu {
   /* max-height: 0; */
   z-index: 100;
   position: absolute;
   right: 0;
   width: 100%;
   transition: 0.5s;
   /* transform: translateY(-400px); */
}

.side-menu ul {
   height: 0;
   text-align: center;
   overflow: hidden;
   background-color: #FFFFFF;
   background-color: var(--menu-background-color);
   transition: height 0.5s;
}
.side-menu ul li {
   display: none;
   margin: 20px 0;
   /* padding: 20px 0; */
}
.side-menu ul a {
   display: block;
   border-bottom: 2px solid #000;
   padding: 20px;
   color: #000000;
   color: var(--menu-color);

}
.side-menu ul li:last-child a {
   border-bottom: none;
}
/* .side-menu ul li a {
   padding: 0 20px;
   color: var(--menu-color);
   text-transform: uppercase;
} */
.logo {
   display: flex;
   align-items: center;
   margin-left: 50px;
}
.logo img {
   width: 40px;
   height: 40px;
   border-radius: 50%;
}
.logo h3 {
   text-transform: uppercase;
   font-weight: 700;
   padding-left: 20px;
   color: #000000;
   color: var(--menu-color);
}

.hamburger-menu {
   display: none;
}

@media only screen and (max-width: 1000px) {
   .main-menu ul {
      display: none;
   }

   .side-menu.show ul{
      height: 100%;
   }
   .side-menu.show ul li {
      display: block;
   }

   .logo {
      margin-left: 10px;
   }
   .hamburger-menu {
      display: block;
      position: relative;
      width: 70px;
      height: 100%;
   }
   .hamburger-menu span {
      display: block;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%,-50%);
      width: 30px;
      height: 5px;
      background-color: #000000;
      background-color: var(--menu-color);
      border-radius: 10px;
      transition: .5s;
      box-shadow: 0 2px 2px #555555;
      box-shadow: 0 2px 2px var(--menu-shadow);
   }
   .hamburger-menu span:nth-child(1) {
      transform: translate(-50%,-50%) translateY(-14px);
      transition: .5s;
   }
   .hamburger-menu span:nth-child(4) {
      transform: translate(-50%,-50%) translateY(14px);
      transition: .5s;
   }
   .hamburger-menu.active span:nth-child(1) {
      transform: translate(-50%,-50%) translateY(-35px);
      opacity: 0;
   }
   .hamburger-menu.active span:nth-child(4) {
      transform: translate(-50%,-50%) translateY(35px);
      opacity: 0;
   }
   .hamburger-menu.active span:nth-child(2) {
      transform: translate(-50%,-50%) rotate(-45deg);
      transition-delay: .2s;
   }
   .hamburger-menu.active span:nth-child(3) {
      transform: translate(-50%,-50%) rotate(45deg);
      transition-delay: .2s;
   }
}
