:root {
   --button-primary: #FFFFFF;
}

.reflective_bttn  {
   position: relative;
   display: inline-block;
   padding: 25px 30px;
   margin: 40px 0;
   color: #FFFFFF;
   color: var(--button-primary);
   text-decoration: none;
   text-transform: uppercase;
   transition: 0.5s;
   letter-spacing: 4px;
   overflow: hidden;
   margin-left: 30px;
   margin-right: 50px;
   border-radius: 5px;
}

.reflective_bttn :hover{
   background: #FFFFFF;
   background: var(--button-primary);
   color: #050801;
   box-shadow: 0 0 5px #FFFFFF,
               0 0 25px #FFFFFF,
               0 0 50px #FFFFFF,
               0 0 200px #FFFFFF;
   box-shadow: 0 0 5px var(--button-primary),
               0 0 25px var(--button-primary),
               0 0 50px var(--button-primary),
               0 0 200px var(--button-primary);
    -webkit-box-reflect:below 1px linear-gradient(transparent, #0005);
}


.reflective_bttn span{
   position: absolute;
   display: block;
}
.reflective_bttn span:nth-child(1){
   top: 0;
   left: 0;
   width: 100%;
   height: 2px;
   background: linear-gradient(90deg,transparent,#FFFFFF);
   background: linear-gradient(90deg,transparent,var(--button-primary));
   animation: animate1 1s linear infinite;
}
@keyframes animate1{
   0%{
      left: -100%;
   }
   50%,100%{
      left: 100%;
   }
}
.reflective_bttn span:nth-child(2){
   top: -100%;
   right: 0;
   width: 2px;
   height: 100%;
   background: linear-gradient(180deg,transparent,#FFFFFF);
   background: linear-gradient(180deg,transparent,var(--button-primary));
   animation: animate2 1s linear infinite;
   animation-delay: 0.25s;
}
@keyframes animate2{
   0%{
       top: -100%;
   }
   50%,100%{
       top: 100%;
   }
}
.reflective_bttn span:nth-child(3){
   bottom: 0;
   right: 0;
   width: 100%;
   height: 2px;
   background: linear-gradient(270deg,transparent,#FFFFFF);
   background: linear-gradient(270deg,transparent,var(--button-primary));
   animation: animate3 1s linear infinite;
   animation-delay: 0.50s;
}
@keyframes animate3{
   0%{
       right: -100%;
   }
   50%,100%{
       right: 100%;
   }
}


.reflective_bttn span:nth-child(4){
   bottom: -100%;
   left: 0;
   width: 2px;
   height: 100%;
   background: linear-gradient(360deg,transparent,#FFFFFF);
   background: linear-gradient(360deg,transparent,var(--button-primary));
   animation: animate4 1s linear infinite;
   animation-delay: 0.75s;
}
@keyframes animate4{
   0%{
       bottom: -100%;
   }
   50%,100%{
       bottom: 100%;
   }
}
#contact-me {
   background-color: #1c1c1c;
   background-image: url(/static/media/topography.2719b4a1.png);
   position: relative;
   width: 100%;
   height: 100vh;
}

.contact-container {
   width: 100%;
   padding: 20px;
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-content: center;
   align-items: center;
}
.contact-container form {
   width: 80%;
}
.contact-container h2 {
   color: var(--button-primary);
   text-align: center;
   margin-bottom: 10px;
}

.contact-container p {
   color: var(--button-primary);
   text-align: center;
   margin-bottom: 10px;
}

.contact-container .row100 {
   position: relative;
   width: 100%;
   display: grid;
   grid-template-columns: repeat(autofit,minmax(300px,1fr));
}

.contact-container .row100 .col {
   position: relative;
   width: 100%;
   padding: 0 10px;
   margin: 30px 0 10px;
   transition: 0.5s;
}

.contact-container .row100 .inputBox {
   position: relative;
   width: 100%;
   height: 40px;
   color: var(--button-primary);
}

.contact-container .row100 .inputBox input,
.contact-container .row100 .inputBox.textarea textarea  { 
   position: absolute;
   width: 100%;
   height: 100%;
   background: transparent;
   box-shadow: none;
   border: none;
   outline: none;
   font-size: 18px;
   padding: 0 10px;
   z-index: 1;

}

.contact-container .row100 .inputBox .text { 
   position: absolute;
   top: 0;
   left: 0;
   line-height: 40px;
   font-size: 18px;
   padding: 0 10px;
   display: block;
   transition: 0.5;
   pointer-events: none;
}

.contact-container .row100 .inputBox input:focus + .text,
.contact-container .row100 .inputBox input:valid + .text  { 
   top: -35px;
   left: -10px;
}

.contact-container .row100 .inputBox .line {
   position: absolute;
   bottom: 0;
   display: block;
   width: 100%;
   height: 2px;
   background: var(--button-primary);
   transition: 0.5s;
   border-radius: 2px;
   pointer-events: none;
}

.contact-container .row100 .inputBox input:focus ~ .line,
.contact-container .row100 .inputBox input:valid ~ .line  { 
   height: 100%;
}

.contact-container .row100 .inputBox.textarea {
   position: relative;
   width: 100%;
   height: 100px;
   padding: 10px 0;
}

.contact-container .row100 .inputBox.textarea textarea {
   height: 100%;
   resize: none;
}
.contact-container .row100 .inputBox textarea:focus + .text,
.contact-container .row100 .inputBox textarea:valid + .text  { 
   top: -35px;
   left: -10px;
}
.contact-container .row100 .inputBox textarea:focus ~ .line,
.contact-container .row100 .inputBox textarea:valid ~ .line  { 
   height: 100%;
}
